import React from "react";
import { useParams } from "react-router-dom";
import Loading from "../components/Loading";

function SingleCocktail() {
  const { id } = useParams();
  const [loading, setLoading] = React.useState(true);
  const [cocktail, setCocktail] = React.useState([]);

  const fetchSingleCocktail = React.useCallback(async () => {
    const response = await fetch(
      `https://www.thecocktaildb.com/api/json/v1/1/lookup.php?i=${id}`
    );
    const cocktail = await response.json();
    if (cocktail.drinks) {
      const {
        strDrink: name,
        strDrinkThumb: image,
        strAlcoholic: info,
        strCategory: category,
        strGlass: glass,
        strInstructions: instructions,
        strIngredient1,
        strIngredient2,
        strIngredient3,
        strIngredient4,
        strIngredient5,
      } = cocktail.drinks[0];

      const ingredients = [
        strIngredient1,
        strIngredient2,
        strIngredient3,
        strIngredient4,
        strIngredient5,
      ];
      const newCocktail = {
        name,
        image,
        info,
        category,
        glass,
        instructions,
        ingredients,
      };
      setCocktail(newCocktail);
      setLoading(false);
    }
  }, [id]);

  React.useEffect(() => {
    fetchSingleCocktail();
  }, [id, fetchSingleCocktail]);

  if (loading) {
    return <Loading />;
  }
  if (!cocktail) {
    return <h2>no</h2>;
  } else {
    const {
      name,
      image,
      info,
      category,
      glass,
      instructions,
      ingredients,
    } = cocktail;
    return (
      <section className='single-cocktail-container'>
        <div className='title'>
          <h2>{name}</h2>
          <div className='s-cocktail'>
            <img src={image} alt={name} />
            <div className='s-cocktail-info'>
              <p>
                <span className='tag'>Name :</span>
                {name}
              </p>
              <p>
                <span className='tag'>Category :</span>
                {category}
              </p>
              <p>
                <span className='tag'>Info :</span>
                {info}
              </p>
              <p>
                <span className='tag'>Glas :</span>
                {glass}
              </p>
              <p>
                <span className='tag'>Instructions :</span>
                {instructions}
              </p>
              <p>
                <span className='tag'>ingredients: </span>
                {ingredients.map((ingredient, index) => {
                  return ingredient ? (
                    <span key={index}>{ingredient}, </span>
                  ) : null;
                })}
              </p>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default SingleCocktail;
